@import url(https://fonts.googleapis.com/css2?family=Inter:wght@600;800&family=Poppins&display=swap);
body {
  width: 100%;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-family: "Poppins", sans-serif;
}

.box {
  background-color: white;
}

h3 {
  margin: auto 10px;
  color: #333333;
}

.arrow {
  color: #333333;
}

.icon {
  color: skyblue;
}

.searchIcon {
  margin-left: auto;
  color: #333333;
}

.avatar {
  margin-left: 20px;
}

span {
  color: red;
  font-size: 17px;
}
.bottom {
  display: flex;
  justify-content: center;
}

.card {
  width: 176px;
}

.cardLayout {
  display: flex;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
}

.heading {
  padding-left: 10px;
  margin-top: 10px;
}

.layoutPage {
  z-index: 99;
  border-radius: 10px;
}

.header2 {
  height: 100%;
  width: 100%;
  position: relative;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

video::-webkit-media-controls-timeline {
  display: none;
}

.content {
  position: absolute;
  color: white;
  left: 30%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  bottom: 10%;
}
