.header2 {
  height: 100%;
  width: 100%;
  position: relative;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

video::-webkit-media-controls-timeline {
  display: none;
}

.content {
  position: absolute;
  color: white;
  left: 30%;
  width: fit-content;
  bottom: 10%;
}
